	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Данное упражнение может вызвать затруднение, так как присутствует статодинамическая работа. Выполнять рекомендуется в медленном темпе, увеличивая время под нагрузкой и лучшей проработке мышц.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Выровняйте петли, чтоб оба конца были ровны.</li>\n  <li>Возьмитесь за рукояти, пройдите вперед до полного натяжения лямок.</li>\n  <li>Ладони обращены друг к другу, руки чуть согнуты в локтях.</li>\n  <li>Отводим руки в стороны, равномерно распределяя вес тела на обе руки, опускаемся до комфортного растяжения грудных мышц.</li>\n  <li>Плавным движением возвращаем руки в исходное положение.</li>\n  <li>Так повторять заданное количество повторений.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Избегайте провисание корпуса</li>\n  <li>Держите руки чуть согнутыми, это снимет нагрузку с локтевых суставов</li>\n  <li>Резкие движения исключить</li>\n  <li>Не смещайте нагрузку на «нецелевые» мышцы.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/6SYt0fKADkM\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/razvedenie-ruk-s-petlyami","efficiency":"6","title":"Разведение рук с петлями","image":"/static/0-12-0.png","image_2":"/static/0-12-1.png","image_3":"","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"3","bodypart":"Грудь","filtertype":"base","type":"Базовое","filterinventory":"trx","inventory":"TRX Петли","filterplace":""}}},"pathContext":{}}

